"use client";

import { addTodo } from "@/src/api";
import React, { ChangeEvent, FormEvent, useState } from "react";
import { v4 as uuidv4 } from "uuid";

// onTaskAdded を親コンポーネントから受け取る
const AddTask = ({ onTaskAdded }: { onTaskAdded: () => void }) => {
  const [taskTitle, setTaskTitle] = useState("");

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault();

    const newTask = { id: uuidv4(), text: taskTitle };

    try {
      await addTodo(newTask);  // タスクを追加
      setTaskTitle("");  // 入力フィールドをクリア
      onTaskAdded();  // 親コンポーネントにタスク追加後の通知
    } catch (error) {
      console.error("Error adding task:", error);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="mb-4 space-y-3">
      <input
        type="text"
        className="w-full border px-4 py-2 rounded-lg focus:outline-none focus:border-blue-400"
        onChange={(e: ChangeEvent<HTMLInputElement>) => setTaskTitle(e.target.value)}
        value={taskTitle}
      />
      <button className="w-full px-4 py-2 text-white bg-blue-500 rounded transform hover:bg-blue-400 hover:scale-95 duration-200">
        Add Task
      </button>
    </form>
  );
};

export default AddTask;
